<template>
  <el-container class="main">
      <section>
          <div class="content">
            <img src="../../../../../assets/fail.png" alt="">
          </div>
          <a href="javascript:;" class="gohome status__label">
            回调失败
          </a>
          <a  class="gohome btn_back" @click="backToHome">
            重新回调
          </a>
      </section>
  </el-container>
</template>

<script>
export default {
  name: 'Page401',
  methods: {
    backToHome () {
      this.$router.push('/electronic-signature')
    }
  }
}
</script>

<style lang="scss" scoped>
.main{
  display: flex;
  @include flexCenter;

  >section {
    display: inherit;
    @include flexCenter;
    flex-direction: column;
    width:400px;
    height:200px;
    margin-top:15%;
    border-radius:15px;
    img{
      width:512px;
    }

    .gohome{
      margin-top:25px;
      display:block;
      text-decoration:none;
      letter-spacing: 0.04px;
      font-size: 14px;
    }
    .status__label{
      font-family: PingFangSC-Regular;
      color: #9B9B9B;
    }
    .btn_back{
      display:block;
      background: #66B1FF;
      border-radius: 25px;
      width:180px;
      height:36px;
      line-height:36px;
      color: #FFFFFF;
      text-align:center;
      cursor:pointer;
    }

  }
}
</style>



